.error_modal {
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(0, 0, 0); /* Fallback color */
    background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */
}
.error_container{
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.error_content{
    width: 30%;
    background-color: #FFFFFF;
    font-family: "Roboto", sans-serif;
}
.center_element{
    display: flex;
    flex-direction: row;
    justify-content: center;
}
#snap_text{
    font-weight: bolder;
    padding-top: 15px;
}
#error_text{
   overflow-wrap: break-word;
    margin-top: 15px;
    font-size: 16px;
}
#dismiss_btn{
    background-color: #e8505a;
    color: #FFFFFF;
    text-align: center;
    padding: 10px;
    margin-top: 20px;
    cursor: pointer;
    font-weight: bolder;
}
.error_icon{
    color: #e8505a;
    font-size: 40px;
    margin-top: 10px;
}